<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('InterviewerList') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>

                    </div>
                    <div class="modal-body">
                            <table class="table table-bordered mb-0">
                                <tbody>
                                    <tr v-if="noRecords">
                                        <td colspan="10" class="text-center">
                                            <span class="text-danger no-record">{{ $t('NoRecordfound')}}</span>
                                        </td>
                                    </tr>
                                    <tr v-for="item in userdata">   
                                        <td colspan="10" class="text-left">
                                            <span> {{item.name}}</span>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from './../../../services/DataService';
export default {
    props: {
        edititem: {
            type: Object,
            required: true
        },
        itemindex: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            userdata: [],
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = this.GetUserInfo.ID;
        vm.companyId = this.GetUserInfo.CompanyId;
        await vm.GetDdlRecords_interview();
    },
    methods: {
        GetDdlRecords_interview: async function () {
            var vm = this;
            var url = `ids=&type=HR_INTERVIEW&search=&refId=${vm.edititem.GroupsData}`;
            await DataServices.GetDDLData(url).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.userdata=response.data.DATA;
                    }
                }
            });
        },
        Close: function () {
            this.$parent.showAddEditModal = false;
        }
    }
}
</script>
    
    
    