<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AddStage') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form ref="AddStageData" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        docArr: {
            type: Object,
            required: true
        },
        docId: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            docId:0,
            moduleId:0,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('StageName'),
                            name: "StageName",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('PhaseName'),
                            name: "PhaseName",
                            value: "",
                            validationRules: {
                                "required": false
                            },
                            config:{
                                options: [],
                            },
                            disabled:false                         
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "SelectField",
                            label: this.$t('StageType'),
                            name: "StageType",
                            value: "",
                            placeholder: this.$t('Stage'),
                            validationRules: {
                                "required": false
                            },
                            config:{
                                options: [],
                            },
                            disabled: true                         
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            validationRules: "",
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        var vm=this;
        await vm.GetDdlRecords_Phase();
        await vm.GetDdlRecords_PhaseType();
        this.FetchData();
    },
    methods: {
        FetchData: function () {
            var vm = this;
            if(vm.docId>0){
                (vm.FormSchema)[0].Data[0].value = vm.docArr.row.STAGE_NAME;
                (vm.FormSchema)[0].Data[1].value = vm.docArr.row.PHASE_ID;
                (vm.FormSchema)[1].Data[0].value = vm.moduleId;
                (vm.FormSchema)[1].Data[1].value = 1001;
                (vm.FormSchema)[2].Data[0].value = vm.docArr.row.STAGE_DESC;
            }
        },
        Close: function () {
            this.$parent.AddEditStage = false;
        },
        GetDdlRecords_Phase: async function () {
            var vm = this;
            vm.CountryData = [];
            var params = `ids=&moduleName=&type=PHASE_NAME&search=&refId=`
            await DataServices.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[0].Data[1].config.options = response.data.DATA;
                }
                vm.$refs.AddStageData.UpdateKeyValue();
            })
        },
        GetDdlRecords_PhaseType: async function () {
            var vm = this;
            vm.CountryData = [];
            vm.FormSchema[1].Data[0].config.options=[];
            var params = `ids=&moduleName=&type=PHASE_TYPE&search=&refId=`
            await DataServices.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[1].Data[0].config.options = response.data.DATA;
                    response.data.DATA.forEach(function (itm) {
                      if(itm.name=='Hiring') {
                        vm.moduleId=itm.value;
                      } else{
                        vm.FormSchema[1].Data[0].value='';
                      }
                });
                }
                vm.$refs.AddStageData.UpdateKeyValue();
                vm.FormSchema[1].Data[0].value=vm.moduleId;
            })
        },
        
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                StageId: vm.docId,
                StageName:formfields.StageName,
                Description: formfields.Description,
                StatusId: formfields.Status,
                PhaseId: formfields.PhaseName,
                StageType: formfields.StageType
            };
            var params = JSON.stringify(jsonobj);
            DataServices.AddInterviewStage(params).then((response) => {
                if (response.data.status == 'Success') {
                    if (vm.docId > 0) {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Document")]), "success", true, vm.$t("Alert"));
                    }else{
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Document")]), "success", true, vm.$t("Alert"));
                    }
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Document")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>