<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('CAMANAGESTAGE') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <tg-list :IsShowAction="false" ModuleName="HR" SubModuleCode="Area" :LegendArray="[]"
                                :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
                                :widgets="[]" IdentityColumn="STAGE_ID" :listType="['List']" :ListData="GroupData"
                                :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                                :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
                                :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                                v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                                <template v-slot:slotdata="{ data }">
                                <template v-if="data.column.COLUMN_NAME == 'STAGE_NAME'">
                                    <a href="#" @click="openAddDocument(data)">{{
                                        data.row.STAGE_NAME }}</a>
                                </template>
                            </template>
                            </tg-list>
                            <div class="modal-footer">
                                <a href="javascript:;" v-on:click="Close" class="btn btn-danger formbtn">
                                    <i class="fa fa-times"></i> {{ $t('Close') }}
                                </a>
                            </div>
                    </div>
                </div>
            </div>
        </div>
        <AddEditStageDataPopup v-if="AddEditStage" :docArr="docArr" :docId="docId"></AddEditStageDataPopup>  
    </div>
    </template>
    <script>
   import DataServices from "@/services/DataService";
    import Schema from '../Candidate/SkilSchema'
    import AddEditStageDataPopup from '../../HR/JobPost/AddEditStageDataPopup.vue'
    export default {
        props: {
            JobId: {
            type: Object,
            required: true
        },
        },
        components: { AddEditStageDataPopup },
        data(){
            return{
                isLoading:false,
                HeaderText:'',
                docArr:[],
                noRecords: false,
                AddDocumentPopup: false,
                listheaderbuttons: Schema.buttons,
                Headers:[{
                    "COLUMN_NAME": "STAGE_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Stage',
                    "DISPLAY_ORDER": 1,
    
                },
                {
                "COLUMN_NAME": "STAGE_DESC",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Stage Description',
                "DISPLAY_ORDER": 2
            }], 
                SortBy:'',          
                skillStringArr:[],
                SortExp:'',
                TotalRecords: 0,
                searchCondition:'',
                PageNumber: 1,
                PageSize:10,         
                GroupData: [],           
                CurrentPage: 1,
                TotalPages: 0,    
                skillString: '',
                AddEditStage: false
            }
        },
        created:function(){
            var vm=this;
            vm.FetchData();
        },
        methods:{
            renderActions: function (row) {
               
            },
            commonHeaderButtonClick: function (event) {
                switch (event.callbackfunction) {
                    case "openAddSkill":
                        this.openAddDocument();
                        break;
                    case "commonDeleteData":
                        this.commonDeleteData();
                        break;
                }
            },
            commonDeleteData: function (id) {            
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {                   
                    var url=`ids=${val}&tablename=TALYGEN_PROJECT_STAGE&key=stage_id`;                 
                    DataServices.DeleteItems(url).then(function (response) {                        
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Stage')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Stage')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
            actionButtonClick: function (callbackfunction, item, index, event) {
                
            },
            openAddDocument: function (item) {
            var vm =this;
            if (item == undefined){
                vm.docId=0;
                vm.docArr = [];
            }else{
                vm.docId=item.row.STAGE_ID;
                vm.docArr = item;
            }
            this.AddEditStage = !this.AddEditStage;
        },
            sortdata: function (sortBy) {
                this.SortBy = sortBy;
                var sortExp = 'ASC';
                if (this.SortExp == 'ASC') {
                    sortExp = 'DESC';
                }
                this.SortExp = sortExp;
                this.SortBy = sortBy;
                this.SortExp = sortExp;
                this.PageNumber = 1;
                this.FetchData();
            },
            pagerMethod: function (value) {
                this.PageSize = value.PageSize;
                this.PageNumber = value.PageNumber;
                this.FetchData();
            },
            leftCommonSearch: function (value) {
                this.PageNumber = value.PageNumber;
                this.isPaged = 1;
                if (typeof value.searchCondition != 'undefined') {
                    this.searchCondition = value.searchConditionJson;
                }
                else {
                    this.searchCondition = "";
                }
                this.FetchData();
            },
            FetchData: async function(){            
            var vm = this;
            var url = `pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&jobId=${vm.JobId}&type=Hiring`;
            await DataServices.GetStagesData(url).then(function (response) {
                if (response.data && response.data.DATA.length > 0) {
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "STAGE_NAME") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vm.GroupData = response.data.DATA;
                    for (var row in vm.GroupData) {
                            vm.GroupData[row].isCheckBoxDisabled = false;
                            if (vm.GroupData[row].IsAssociated == 1) {
                                vm.GroupData[row].isCheckBoxDisabled = true;
                            }
                        }
                    if (vm.GroupData.length > 0) {
                        vm.TotalRecords = vm.GroupData[0].TotalRecords;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.listheaderbuttons[1].isdisabled = true;
                        vm.noRecords = false;
                    }
                    else {
                        vm.noRecords = true;
                    }
                }
                else {
                    vm.noRecords= true;
                    vm.TotalRecords = 0
                    vm.GroupData=[];
                }
            })
            },
            Close:function(){
                var vm =this;
                this.$parent.showmanageStageModal=false;
            }
        }
    }
    </script>
    
    
    