<template>
    <div class="tab-pane mb-0">
        <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
            <div class="expand-list">
                <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                    <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                </a>
            </div>
            <loader :is-visible="isLoading"></loader>
            <tg-list :IsShowAction="true" ModuleName="HR" SubModuleCode="HR_JOB" :RenderRowActionMethod="renderActions"
                v-on:ActionButtonClick="actionButtonClick" IdentityColumn="STAGE_ID" :HeaderText="$t('StageList')"
                :listType="['List']" :ListData="filterItemData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
                :TotalRecords="TotalRecord" :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData"
                :showCheckBox="false" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:LeftsearchButtonClick="leftCommonSearch" :CurrentPageNumber="PageNumber"
                v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'StagesData'">
                        <div class="form-group ml-4">
                            <label>{{ $t('Stage') }}:</label>
                            <div class="input-group">
                                <select v-model="data.row.StagesData" v-bind="{ name: 'Stage_' + index }"
                                    v-bind:disabled="data.row.IsAssociated == true" v-validate="'required'"
                                    v-bind:class="{ 'form-control': true }"
                                    v-on:change="getFloorPlanByActivity(data.row, index)">
                                    <option value="">{{ $t('SelectStages') }}</option>
                                    <option v-for="st in StagesData" v-bind:value="st.value">
                                        {{ st.name }}
                                    </option>
                                </select>
                                <a class="btn-dark theme-primary round-icon-small" v-on:click="openmanageStagePopup()"><i
                                        class="fa fa-wrench text-white pt-7 font-18"></i></a>
                            </div>
                            <!-- <span v-show="errors.has('ManageData.Stage_' + index)"
                                v-bind:class="{ 'text-danger': true, 'is-invalid': errors.has('ManageData.Stage_' + index) }">Stage
                                is required.</span> -->
                        </div>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'GroupsData'">
                        <div class="form-group ml-4">
                            <label>{{ $t('Group') }}:</label>
                            <div class="input-group">
                                <select v-model="data.row.GroupsData" v-bind="{ name: 'Group_' + index }"
                                    v-validate="'required'" v-bind:class="{ 'form-control': true }">
                                    <option value="">{{ $t('SelectGroup') }}</option>
                                    <option v-for="gr in GroupsData" v-bind:value="gr.value">
                                        {{ gr.name }}
                                    </option>
                                </select>
                                <a v-on:click="openManagePopUpGroup()" class="btn-dark theme-primary round-icon-small"><i
                                        class="fa fa-wrench text-white pt-7 font-18"></i></a>
                            </div>
                            <!-- <span v-show="errors.has('ManageData.Group_' + index)"
                                v-bind:class="{ 'text-danger': true, 'is-invalid': errors.has('ManageData.Group_' + index) }">Group
                                is required.</span> -->
                        </div>
                    </template>
                </template>
            </tg-list>
            <div class="">
                <div class="search-btm-btn search-btm-btn d-flex justify-content-end" style="margin-top: 15px;">
                    <a href="javascript:;" class="btn btn-info formbtn mb-2 mr-2" v-on:click="AddRow()"
                        title="Add Interview Stage"><i class="fa fa-plus text-light pr-2 "></i>{{ $t('AddInterviewStage')
                        }}</a>
                    <a href="javascript:;" title="Save" v-if="savebutton" v-on:click="SaveGroup()"
                        class="btn btn-success mb-2"><i class="fa fa-save pr-2"></i>{{ $t('Save') }} </a>
                </div>
                <AddinterviewerListPopup v-if="showAddEditModal" :edititem="edititem" :itemindex="itemindex">
                </AddinterviewerListPopup>
                <AddEditStagePopup v-if="showmanageStageModal" :JobId="JobId"></AddEditStagePopup>
                <AddEditGroupPopup v-if="showmanageGroupModal" :JobId="JobId"></AddEditGroupPopup>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../../services/DataService';
import AddinterviewerListPopup from './AddinterviewerListPopup.vue'
import AddEditStagePopup from './AddEditStagePopup.vue'
import AddEditGroupPopup from './AddEditGroupPopup.vue'
export default {
    components: { AddinterviewerListPopup, AddEditStagePopup, AddEditGroupPopup },
    data() {
        return {
            isLoading: false,
            SortBy: '',
            SortExp: '',
            PageSize: 10,
            PageNumber: 1,
            TotalRecord: 0,
            CurrentPage: 1,
            edititem: [],
            itemindex: 0,
            listheaderbuttons: '',
            leftSearchFields: '',
            noRecord: '',
            filterItemData: [],
            JobId: 0,
            GroupData: [],
            Data: [],
            savebutton: '',
            ddlStages: [],
            StagesData: [],
            GroupsData: [],
            is_schedule_interview: '',
            showAddEditModal: false,
            showmanageStageModal: false,
            showmanageGroupModal: false,
            Headers: [{
                "COLUMN_NAME": "StagesData",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Interview Stages",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "GroupsData",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Interviewer Groups",
                "DISPLAY_ORDER": 2
            }
            ],
            listActions: [{
                title: 'View',
                href: 'javascript:void(0)',
                icon: 'fa fa-eye text-info action-icon',
                callbackfunction: 'ViewonAction',
                additionalSpan: ''
            },
            {
                title: 'Delete',
                href: 'javascript:void(0)',
                icon: 'fa fa-trash text-danger action-icon',
                callbackfunction: 'DeleteOnAction',
                additionalSpan: ''
            }
            ]
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = this.GetUserInfo.ID;
        vm.companyId = this.GetUserInfo.CompanyId;
        vm.JobId = this.$route.params.id;
        await vm.GetGroups();
        await vm.GetDdlRecords_stages();
        await vm.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `companyId=${vm.companyId}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&jobId=${vm.JobId}&type=Hiring`;
            await DataService.InterviewierDetails(url).then(function (response) {
                vm.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == "StagesData") {
                        column.settings = {
                            isInSlot: true,
                        };
                    }
                    if (column.COLUMN_NAME == "GroupsData") {
                        column.settings = {
                            isInSlot: true,
                        };
                    }
                });
                var returndata = response.data.recruiterData;
                if (returndata.length > 0) {
                    vm.savebutton = true;
                }
                if (vm.filterItemData != null) {
                    $.each(returndata, function (index, item) {
                        if (item.IS_ASSOCIATED) {
                            vm.is_schedule_interview = true;
                        }
                        var obj = {
                            StagesData: item.STAGEID,
                            GroupsData: item.GROUPID,
                            IsAssociated: item.IS_ASSOCIATED
                        };
                        vm.filterItemData.push(obj);
                    });
                }
            });
        },
        GetDdlRecords_stages: async function () {
            var vm = this;
            var url = `ids=&&&type=HR_COMPANYSTAGES&search=&refId=`;
            await DataService.GetDDLData(url).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.StagesData = response.data.DATA;
                    }
                }
            });
        },
        GetGroups: async function () {
            var vm = this;
            var url = `ids=&moduleName=&type=HR_GROUPS&search=&refId=`;
            await DataService.GetDDLData(url).then(function (response) {
                vm.GroupsData = response.data.DATA;
            })
        },
        getFloorPlanByActivity: function (item, SelectedActivtyIndex) {
            var vm = this;
            debugger;
            var filterItem = vm.filterItemData.sort();
            filterItem.reduce(function (item, e1) {
                var isFalse = true;
                var matches = item.filter(function (e2) {
                    if (e1.StagesData == e2.StagesData) {
                        if (isFalse) {
                            vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Stage")]), "warning", true, vm.$t("Alert"));
                        }
                        e1.StagesData = "";
                        e1.StagesData = null;
                        isFalse = false;
                        return false;
                    }
                });
                if (matches.length == 0) {
                    item.push(e1);
                }
                return item;
            },
                []);
        },
        openmanageStagePopup: function () {
            var vm = this;
            vm.showmanageStageModal = !vm.showmanageStageModal;
        },
        openManagePopUpGroup: function () {
            var vm = this;
            vm.showmanageGroupModal = !vm.showmanageGroupModal;
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'View':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {

            }
        },
        leftCommonSearch: function (value) { },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'ViewonAction':
                    this.openPopUp(item, index)
                    break;
                case 'DeleteOnAction':
                    this.DeleteRow(item, index)
                    break;
            }
        },
        openPopUp: function (item, index) {
            var vm = this;
            if (item == undefined) {
                vm.itemindex = 0;
                vm.edititem = [];
            } else {
                vm.itemindex = index;
                vm.edititem = item;
            }
            vm.showAddEditModal = !vm.showAddEditModal;
        },
        AddRow: function () {
            debugger;
            var vm = this;
            if (vm.is_schedule_interview) {
                vm.ShowAlert(vm.$t('InterviewProgress'), "warning", true, vm.$t("Alert"));
                return false;
            }
            var obj = {
                StagesData: "",
                GroupsData: "",
                IsAssociated: false
            };
            vm.filterItemData.push(obj);
            vm.Record = false;
            vm.savebutton = true;
        },
        DeleteRow: async function (item, index) {
            var vm = this
            if (vm.is_schedule_interview) {
                vm.ShowAlert(vm.$t('cantDeleteStage'), "warning", true, vm.$t("Alert"));
                return false;
            }
            if (index === 0) {
                vm.confirmR(vm.$t("ConfirmDeleteField"), true, 'Confirm', function () {
                    //vm.filterItemData();
                });
            }
            else {
                vm.confirmR(vm.$t("ConfirmDeleteField"), true, 'Confirm', async function () {
                    var data = {
                        GroupId: item.GroupsData,
                        StageId: item.StagesData,
                        UserId: vm.userId,
                        CompanyId: vm.companyId,
                        jobid: vm.JobId,
                    };
                    var stagePostString = JSON.stringify(data);
                    await DataService.DeleteCandidateByPostId(stagePostString).then(response => {
                        if (response.data.Status == "Success") {
                            vm.filterItemData.splice(index, 1);
                        }
                        else if (response.data.Status == "Scheduled") {
                            vm.ShowAlert(vm.$t('cantDeleteStage'), "warning", true, vm.$t("Alert"));
                        }
                    }, function (response) {
                    });
                });
            }
        },
        SaveGroup: function () {
            var vueObj = this;
            debugger;
            if (vueObj.is_schedule_interview) {
                vueObj.ShowAlert(vueObj.$t('CantUpdateStage'), "warning", true, vueObj.$t("Alert"));
                return false;
            }
            var groupdata = [];
            $.each(this.filterItemData, function (key, item) {
                var groupIds = 0;
                if (Array.isArray(item.GroupsData)) {
                    groupIds = item.GroupsData;
                }
                else {
                    groupIds = item.GroupsData;
                }
                groupdata.push(groupIds);
            });
            var stagedata = [];
            $.each(this.filterItemData, function (key, item) {
                var stageId = 0;
                if (Array.isArray(item.StagesData)) {
                    stageId = item.StagesData;
                }
                else {
                    stageId = item.StagesData;
                }
                stagedata.push(stageId);
            });
            var keyIDs = [];
            $.each(this.filterItemData, function (key, item) {
                var keyID = 0;
                if (Array.isArray(key + 1)) {
                    stageId = key;
                }
                else {
                    keyID = key + 1;
                }
                keyIDs.push(keyID);
            });
            var data = {
                GroupId: groupdata,
                StageId: stagedata,
                UserId: vueObj.userId,
                CompanyId: vueObj.companyId,
                JobId: vueObj.JobId,
                stageOrder: keyIDs
        }
        var objectPostString = JSON.stringify(data);
        DataService.SaveInterviewerList(objectPostString).then(function (response) {
            if (response.data[0].Status == "Success") {
                vueObj.ShowAlert(vueObj.$t("AddedSuccess", [vueObj.$t("InterviewStages")]), "success", true, vueObj.$t("Alert"));
                vueObj.$parent.loadData("JobDetails");
            }
            else {
                vueObj.ShowAlert(vueObj.$t('InterviewStages'), "warning", true, vueObj.$t("Alert"));
            }
        }, function (response) {
        });
    },
}
}
</script>
