<template>
    <div id="divMainFormData" v-cloak>
        <section id="main-content" class="main-content">
            <loader :is-visible="isLoading"></loader>
            <div class="left-menu-tab div-click-event hr_jobpost_a">
                <div class="col-md-12 p-0">
                    <div class="theme-primary partition" >
                        <span class="p-name text-white textelipsis">{{ $t('Jobdescription') }}</span>
                    </div>
                </div>
                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab" role="tablist">
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 1 }"
                            data-bs-target="#tab-detail-content" href="javascript:void(0)" data-bs-toggle="tab" role="tab"
                            id="tab-Details" v-on:click="loadData('JobDetails');">
                            <em class="fa fa-gavel" aria-hidden="true" aria-selected="true"></em>
                            {{ $t('JobDetails') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 2 }"
                            data-bs-target="#tab-service-content" href="javascript:void(0)" id="tab-service-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('Dashboard');">
                            <em class="fa fa-list-ul" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('Dashboard') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 3 }"
                            data-bs-target="#tab-resource-content" href="javascript:void(0)" id="tab-resource-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('CandidateList');">
                            <em class="fa fa-list-alt" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('CandidateList') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 4 }"
                            data-bs-target="#tab-document-content" href="javascript:void(0)" id="tab-document-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('InterviewStages');">
                            <em class="fa fa-list-ul" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('InterviewStages') }}
                        </a>
                    </li>
                    
                </ul>
            </div>
            <div class="right-content">
                <div class="col-md-12 p-0 mb-2">
                    <div class="theme-primary partition p-actions-expand">
                        <span class="p-actions float-right">
                            <a v-on:click="EditJobPost()" :typetitle="$t('Edit')" :typefor="Edit" class="p-action-btn text-white">
                                <em class="fa fa-pencil"></em>
                                <br />{{ $t('EditJob') }}
                            </a>  
                            <a @click="AddCandidate" title="Add as Candidate" class="p-action-btn text-white"><em
                                class="fa fa-user-plus"></em><br /><span>{{$t('AddCandidate') }}</span>
                            </a>
                            <RouterLink to="/JobPost/JobListing" class="p-action-btn text-white float-right">
                                <em class="fa fa-angle-double-left"></em>
                                <br />{{ $t('BacktoList') }}
                            </RouterLink> 
                        </span>
                    </div>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 1 }"
                    id="tab-resource-content" role="tabpanel" aria-labelledby="tab-resource-content" v-if="isJobDetails">
                    <JobDetail :JobDetailData=JobDetailData :JobId="JobId"></JobDetail>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 2 }"
                    id="tab-resource-content" role="tabpanel" aria-labelledby="tab-resource-content" v-if="isDashboard">
                    <Dashboard :JobDetailData=JobDetailData :JobId="JobId"></Dashboard>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 3 }"
                    id="tab-service-content" role="tabpanel" aria-labelledby="tab-service-content" v-if="isCandidateList">
                    <CandidateList :JobDetailData=JobDetailData :JobTitle="JobTitle" :JobId="JobId"></CandidateList>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 4 }"
                    id="tab-document-content" role="tabpanel" aria-labelledby="tab-document-content" v-if="isInterviewStages">
                    <InterviewStages :JobDetailData=JobDetailData :JobTitle="JobTitle" :JobId="JobId"></InterviewStages>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import JobDetail from './JobDetails.vue';
import Dashboard from './Dashboard.vue';
import CandidateList from './CandidateList.vue';
import InterviewStages from './InterviewStages.vue';
import DataService from '../../../services/DataService';

export default {
    components: { 
        InterviewStages, CandidateList, Dashboard, JobDetail
    },
    data() {
        return {
            JobId: 0,
            isJobDetails: false,
            isDashboard: false,
            isCandidateList: false,
            isInterviewStages: false,
            JobDetailData: [],
            InterviewStages: false,
            CandidateList: false,
            Dashboard: false,
            JobDetail: false
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.JobId = this.$route.params.id;
        await this.FetchData();
        vm.loadData("JobDetails");
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `companyId=${vm.companyId}&userId=${vm.userId }&postId=${vm.JobId }`;
            await DataService.JobDetails(url).then(function (response) {
                vm.JobDetailData = [];
                if (response.data != null && response.data != "") {
                    vm.JobDetailData = response.data.result.data[0];
                    vm.JobTitle = response.data.result.data[0].post_title;
                }
            });
        },
        EditJobPost: function () {
            var vm = this;
            if (vm.JobId > 0) {
                var id = vm.JobId;
                var moduleName = 'Hiring';
                var subModuleCode = 'HR_JOB';

                vm.$router.push({
                    name: 'HRManageJobPost',
                    params: {
                        id: id,
                        moduleName: moduleName,
                        subModuleCode: subModuleCode,
                        reqfrom: '/JobPost/JobListing'
                    }
                });
            }
        },
        AddCandidate: function (ths) {            
            this.$router.push({ name: 'ManageCandidate', params: { reqfrom: 'JobPost/JobListing', id: 0, pid: ths, aid: null, uid: null } });
        },
        loadData: function (tabName, callFrom) {
            var vm = this;
            if (tabName == "JobDetails") {
                vm.isCandidateList = false;
                vm.isDashboard = false;
                vm.isInterviewStages = false;
                vm.isJobDetails = true;
                vm.activeKey = 1;
                vm.openJobDetails();
            } else if (tabName == "Dashboard") {
                vm.isJobDetails = false;
                vm.isCandidateList = false;
                vm.isInterviewStages = false;
                vm.isDashboard = true;
                vm.activeKey = 2;
                vm.openDashboard();
            } else if (tabName == "CandidateList") {
                vm.isJobDetails = false;
                vm.isDashboard = false;
                vm.isInterviewStages = false;
                vm.isCandidateList = true;
                vm.activeKey = 3;
                vm.openCandidateList();
            } else if (tabName == "InterviewStages") {
                vm.isJobDetails = false;
                vm.isDashboard = false;
                vm.isCandidateList = false;
                vm.isInterviewStages = true;
                vm.activeKey = 4;
                vm.openInterviewStages();
            }
        },
        openInterviewStages: function () {
            this.InterviewStages = !this.InterviewStages;
        },
        openCandidateList: function () {
            this.CandidateList = !this.CandidateList;
        },
        openDashboard: function () {
            this.Dashboard = !this.Dashboard;
        },
        openJobDetails: function () {
            this.JobDetail = !this.JobDetail;
        }
    }
}
</script>



