<script>
import DataService from '../../../services/DataService';
import moment from 'moment';
export default {
    data() {
        return {
            JobId: 0,
            itemData: [],
            internalUrl: '', // Initialize with appropriate values
            externalUrl: '',  // Initialize with appropriate values
            storeFrontUrl:'http://hr.{subdomain}.com/',
            copiedSuccess: false
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.CompanyName = vm.GetUserInfo.CompanyName;
        vm.JobId = this.$route.params.id;
        await vm.FetchData();
        this.internalUrl = window.location.href; // Set internal URL to current page URL
        this.externalUrl = this.storeFrontUrl.replace("{subdomain}", this.GetUserInfo.CompanyName.replace(" ", "-").replace(".", "dot")) + "/Default/JobDetail?cid=" + this.GetUserInfo.CompanyId + "&pid=" + this.$route.params.id;
    },
    methods: {
        FetchData: async function () {
                var vm = this;
                var url = `companyId=${vm.companyId}&userId=${vm.userId }&postId=${vm.JobId }`;
                await DataService.JobDetails(url).then(function (response) {
                    vm.itemData = [];
                    if (response.data != null && response.data != "") {
                        vm.itemData = response.data.result.data;
                    }
                });
            },
            TalentPoolDetail: function (JobId, skillIds) {
                var vm = this;
                vm.$router.push({ name: 'ViewTalentPool', params: { pid: JobId ,sid: skillIds, type: 'JobListing' } });
            },
            formatDatee: function (value) {
                if (value) {
                    return moment(value, "YYYY-MM-DD").format(String('MM/DD/YYYY'))
                }
            },
            CandidateTab: function(){
                var vm= this;
                vm.$parent.loadData("CandidateList");
            },
            copyToClipboard: async function (text) {
            try {
                await navigator.clipboard.writeText(text);
                this.copiedSuccess = true;
                setTimeout(() => {
                    this.copiedSuccess = false;
                }, 2000);
            }catch (err) {
                console.error('Failed to copy: ', err);
            }
        } 
    }
}
</script>
