<template>
    <div class="tab-pane mb-0">
        <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
            <div class="expand-list">
                <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                    <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                </a>
            </div>
            <loader :is-visible="isLoading"></loader>
            <tg-list :IsShowAction="true" ModuleName="HR" SubModuleCode="HR_JOB" :RenderRowActionMethod="renderActions"
                v-on:ActionButtonClick="actionButtonClick" IdentityColumn="applicant_id" :HeaderText="$t('CandidateList')"
                :listType="['List']" :ListData="itemData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
                :TotalRecords="TotalRecord" :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData"
                :showCheckBox="false" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:LeftsearchButtonClick="leftCommonSearch" :CurrentPageNumber="PageNumber"
                v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'resume_path'">
                        <a href="#"
                            v-if="data.column.COLUMN_NAME == 'resume_path' && data.row.resume_path != null && data.row.resume_path != ''"
                            v-on:click="DownloadResume(data.row.resume_path, data.row.resume_name)" class="text-primary"><em
                                class="fa fa-download pr-2"></em><span>{{ data.row.resume_name }}</span></a>
                        <span v-if="data.column.COLUMN_NAME == 'resume_path' && (data.row.resume_path == null || data.row.resume_path == '')"
                            class="text-secondary"><em class="fa fa-download pr-2"></em><span>{{ data.row.resume_name }}</span></span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'experience_year'">
                        <span v-if="(data.row.experience_year == 0 && data.row.remaining_months == 0)">{{ $t('NoExperience')}}</span>
                        <span v-if="(data.row.experience_year == null && data.row.remaining_months == null)">{{ $t('NoExperience')}}</span>
                        <span v-if="(data.row.experience_year != null || data.row.remaining_months != null)">{{ data.row.experience_year ==0 ? '' : data.row.experience_year + ' years ' }} {{ data.row.remaining_months == 0 ? '' : data.row.remaining_months + ' months' }} </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'date_applied_on'">
                        <span>
                            {{ $options.filters.formatDate(data.row.date_applied_on) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'star_rating'">
                        <span v-if="(data.row.star_rating == 5)" href="javascript:; "><em
                                class="fa fa-star text-warning"></em><em class="fa fa-star text-warning"></em><em
                                class="fa fa-star text-warning"></em><em class="fa fa-star text-warning"></em><em
                                class="fa fa-star text-warning"></em></span>
                        <span v-if="(data.row.star_rating == 4)" href="javascript:;"><em
                                class="fa fa-star text-warning"></em><em class="fa fa-star text-warning"></em><em
                                class="fa fa-star text-warning"></em><em class="fa fa-star text-warning"></em></span>
                        <span v-if="(data.row.star_rating == 3)" href="javascript:;"><em
                                class="fa fa-star text-warning"></em><em class="fa fa-star text-warning"></em><em
                                class="fa fa-star text-warning"></em></span>
                        <span v-if="(data.row.star_rating == 2)" href="javascript:;"><em
                                class="fa fa-star text-warning"></em><em class="fa fa-star text-warning"></em></span>
                        <span v-if="(data.row.star_rating == 1)" href="javascript:;"><em
                                class="fa fa-star text-warning"></em></span>
                    </template>
                </template>
            </tg-list>
        </div>
    </div>
</template>
    
<script>

import DataService from '../../../services/DataService';
export default {
    data() {
        return {
            isLoading: false,
            SortBy: '',
            SortExp: '',
            PageSize: 10,
            PageNumber: 1,
            TotalRecord: 0,
            CurrentPage: 1,
            listheaderbuttons: '',
            leftSearchFields: '',
            noRecord: '',
            itemData: [],
            Headers: [{
                "COLUMN_NAME": "candidate_name",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Candidates",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "resume_path",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Resume Links",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "experience_year",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Experience",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "date_applied_on",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Applied Date",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "star_rating",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Job Fitment Rating",
                "DISPLAY_ORDER": 5
            }
            ],
            listActions: [{
                title: 'View',
                href: 'javascript:void(0)',
                icon: 'fa fa-eye text-info action-icon',
                callbackfunction: 'ViewonAction',
                additionalSpan: ''
            },
            {
                title: 'Delete',
                href: 'javascript:void(0)',
                icon: 'fa fa-trash text-danger action-icon',
                callbackfunction: 'DeleteOnAction',
                additionalSpan: ''
            }
            ]
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = this.GetUserInfo.ID;
        vm.companyId = this.GetUserInfo.CompanyId;
        vm.JobId = this.$route.params.id;
        await vm.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `postId=${vm.JobId}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&langCode=EN`;
            await DataService.GetCandidateListingByPostId(url).then(function (response) {
                vm.isLoading = false;
                if (response.data != null && response.data != "") {
                    vm.itemData = response.data.result.data;
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "resume_path") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "experience_year") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "date_applied_on") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "star_rating") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vm.TotalRecord = response.data.result.data[0].totalrecords;
                    vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecord = false;
                } else {
                    vm.noRecord = true;
                    vm.itemData = [];
                    vm.TotalRecord = 0

                }
                vm.isLoading = false;
            });
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'ViewonAction':
                        action.isVisible = true;
                        break;
                    case 'DeleteOnAction':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        DownloadResume: async function (path, name) {
            var vm = this;
            var fileName = name;
            var filePath = path;
            if (filePath == "" && fileName == "" || filePath == null) {
                vm.ShowAlert(vm.$t("NoAttachment"), "warning", true, vm.$t("Alert"));
            } else {
                var url = `filePath=${filePath}&fileName=${fileName}`;
                await DataService.DownloadFile(url).then(async function (response) {
                    const byteData = response.data;
                    const fileURL = `data:image/png;base64;base64,${byteData}`;
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', fileName);
                    document.body.appendChild(fileLink);
                    fileLink.click();
                })
            }
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {

            }
        },
        leftCommonSearch: function (value) { },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'ViewonAction':
                    this.ViewCandidateDetail(item.applicant_id,item.post_id)
                    break;
                case 'DeleteOnAction':
                    this.DeleteCandidate(item.applicant_id,item.post_id);
                    break;
            }
        },
        ViewCandidateDetail:function (ApplicantId, JobId){
            var vm = this;
            vm.$router.push({ name: 'CandidateDetail', params: { id: ApplicantId, JobId: JobId } });
        },
        DeleteCandidate: function (id, PostId) {
            var vm = this;  
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Candidate")), function () {
                    var postJSON = {
                        applicant_id: id,
                        company_id: vm.companyId ,
                        user_id:  vm.userId,
                        post_id: PostId,
                    };
                    DataService.DeleteCandidateByPostId(postJSON).then(response => {
                        if (response.data.Status == "Success") {
                            vm.FetchData();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Candidate')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Candidate')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
        },
    }
}
</script>
    