<template>
<div class="tab-pane mb-0">
    <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
        <div class="expand-list">
            <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
            </a>
        </div>
        <section id="main-content" class="main-content no-left-bar">
            <div class="container-fluid padding-t_10">
                <div class="right-content">
                    <ul class="dashbord-widget">
                        <li class="one_wid" target='_blank' >
                            <h3>{{TotalCandidates}}<br><span>{{$t('JobPostTotalCandidates')}}</span></h3>
                            <em class="fa fa-users" aria-hidden="true"></em>
                        </li>
                        <li class="two_wid" target='_blank'>
                            <h3>{{Shortlisted}}<br><span>{{$t('JobPostShortlisted')}}</span></h3>
                            <em class="fa fa-user-circle-o" aria-hidden="true"></em>
                        </li>
                        <li class="three_wid" target='_blank'>
                            <h3>{{hired}}<br><span>{{$t('JobPostHired')}}</span></h3>
                            <em class="fa fa-user-plus" aria-hidden="true"></em>
                        </li>
                        <li class="four_wid" target='_blank'>
                            <h3>{{Rejected}}<br><span>{{$t('Rejected')}}</span></h3>
                            <em class="fa fa-user-times" aria-hidden="true"></em>
                        </li>
                        <li class="five_wid" target='_blank'>
                            <h3>{{Pending}}<br><span>{{$t('Pending')}}</span></h3>
                            <em class="fa fa-user-o" aria-hidden="true"></em>
                        </li>
                    </ul>
                </div>
            </div>
        </section>
    </div>
</div>
</template>

<script>
import DataService from '../../../services/DataService';
export default {
    data() {
        return {
            JobId: 0,
            TotalCandidates: '',
            Shortlisted: '',
            hired: '',
            Rejected: '',
            Pending: ''
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.JobId = this.$route.params.id;
        await vm.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `companyId=${vm.companyId}&userId=${vm.userId }&postId=${vm.JobId }`;
            await DataService.GetDashBoardWidget(url).then(function (response) {
                vm.itemData = [];
                if (response.data != null && response.data != "") {
                    vm.itemData = response.data.result;
                    vm.TotalCandidates = vm.itemData.TOTAL_CANDIDATES;
                    vm.Shortlisted = vm.itemData.SHORTLISTED;
                    vm.hired = vm.itemData.HIRED;
                    vm.Rejected = vm.itemData.REJECTED;
                    vm.Pending = vm.itemData.PENDING;
                }
            })
        }
    }
}
</script>